#root {
  height: 100%
}

.App {
  background-image:linear-gradient(-45deg, #686868 0%,#0e0e0e 100%);
}

.slideOne {
  height: 100vh;

  &_top {
    height: 15vh;
  }

  &_mid {
    height: 30vh;
  }

  &_bottom {
    height: 30vh;
  }

  img {
    background-size: cover;
    max-width: 100%;
    max-height: 100%;

    margin: auto;
    border-radius: 1em;
  }

  .name-heading {
    color: #70747C ;
    font-size: 5rem;
    margin: auto;
    margin-top: 0;
    
text-shadow: 2px 4px 3px rgba(0,0,0,0.3);
  }
}


.slideTwo {
  background-image: linear-gradient(to right, #D1D1D1 0%,#ffffff 49%,#D1D1D1 100%);
  border: 1px solid black;
  //height: 60vh;
  width: 80%;
  margin: auto;
  margin-bottom: 5vh;
  border-radius: 2em;
  box-shadow: 0px 0px 69px 6px rgba(0,0,0,0.75);

  img {
    background-size: cover;
    max-width: 100%;
    max-height: 48%;
    margin: auto;
    border-radius: 1em;
  }

  &_image_text { 
    margin: auto;
  }
}

.slideThree {
  background-image: linear-gradient(to right, #D1D1D1 0%,#ffffff 49%,#D1D1D1 100%);
  border: 1px solid black;
  height: 60vh;
  width: 80%;
  margin: auto;
  margin-bottom: 5vh;
  border-radius: 2em;
  box-shadow: 0px 0px 69px 6px rgba(0,0,0,0.75);
}

.slideFour {
  background-image: linear-gradient(to right, #D1D1D1 0%,#ffffff 49%,#D1D1D1 100%);
  border: 1px solid black;
  height: 60vh;
  width: 80%;
  margin: auto;
  border-radius: 2em;
  box-shadow: 0px 0px 69px 6px rgba(0,0,0,0.75);
}

.contactCard {
  height: 10vh;
  width: 10vh;
  margin-left: 5%;
  margin-right: 5%;
  color: #3E3F47;

  &:hover {
    color:#646872;
  }
}


.navArrow{
  height: 10vh;
  width: 10vh;
  margin: auto;
  margin-bottom: 0;
  color: #70747C ;
}




